.post-enter {
  opacity: 0;
}
.post-enter-active {
  opacity: 1;
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  opacity: 0;
  transition: all 500ms ease-in;
}
/* .post {
  display: block;
  gap: 10px;
