.client {
  margin-bottom: 15px;
}

.app {
  background: #cdd9f0;
  max-width: 100%;
  min-height: 100vh;
  margin: 0 auto;
}

.filter-block {
  padding-top: 30px;
}

.btn-block {
  padding: 20px;
  display: flex;
  gap: 50px;
  justify-content: center;
}

.btn-get {
  padding: 25px 50px;
  color: teal;
  font-size: 14px;
  background: transparent;
  border: 1px solid teal;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-get:hover {
  color: #fff;
  background: teal;
}

.search {
  margin: 0 auto;
  width: 50%;
}
.search-input {
}

.loader {
  display: flex;
  justify-content: center;
  margin-top: 200px;
}

.block-nothing {
  display: flex;
  justify-content: center;
}
