body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clients {
  margin: 0 auto;
  width: 50%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 40px;
}

.title {
}
.client {
  display: flex;
  border: 2px solid #000;
  gap: 10px;
  justify-content: space-between;
}
.client_name {
  padding: 5px;
  text-align: center;
  border-right: 1px solid #000;
}

.client_number {
  padding: 5px;
  text-align: center;
  border-right: 1px solid #000;
}
.client_time {
  padding: 5px;
  text-align: center;
  border-right: 1px solid #000;
}

.client_btn {
  align-self: center;
}

.client_checkbox {
  padding: 5px;
  align-self: center;
}

.page__wrapper {
  padding: 30px;
  display: flex;
  justify-content: center;
}

.page {
  padding: 10px;
  border: 1px solid teal;
  cursor: pointer;
}

.page__current {
  border: 2px solid orange;
  font-weight: bold;
}

.nav__bar-links {
  height: 50px;
  width: 100%;
  display: flex;
  gap: 20px;
  align-items: center;
  padding: 10px 15px;
  background: lightgray;
}
